.list_pictures{
    width: auto;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.picture{
    width: 640px;
    height: auto;
    padding: 0;
    margin: 10px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgb(116, 116, 116);
}
.picture img{
    width: 100%;
    height: 100%;
    border-radius: 5px;
    object-fit: cover;
}
@media all and (max-width:767px){
    .picture{
        width: 90%;
    }
}
    
    