.slider_content{
    width: 100%;
    max-width: 900px;
    height: 100%;
    border-radius: 10px 0 0 10px;
    position: relative;
    background-color: var(--bg_body_dark);
} 
.arrow_container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
}
.slider_img{
    width: 100%;
    max-width: 900px;
    height: 100%;
    border-radius: 10px 0 0 10px;
    object-fit: cover;
    position: absolute;
    top: 0;
}
.slide_nr{
   font-weight: 600;
    font-size: 18px;
    color:var(--slider_nr_color);
    position: absolute;
    bottom: 30px;
    left: 49%;
    text-shadow: 0 0 1px rgb(0, 0, 0);
}
.slider_btn{
    width: auto;
    height: auto;
    background-color: transparent;
    border: none;
}
.slider_btn img{
    width: 50px;
    height: 50px;
    
}
.btn_hidden{
    display: none;
}
@media all and (max-width:767px){
    .slider_content{
        height: 255px;
        border-radius: 0;
    }
    .slider_img{
        border-radius: 0;
    }
    .slider_btn img{
        width: 30px;
        height: 35px;
    }
    .slide_nr{
        font-size: 14px;
        font-weight: 400;
    }

}
@media all and (max-width:540px){
    .slider_content{
        width: 100%;
        height: 320px;
    }
}
@media all and (max-width:370px){
    .slider_content{
        height: 280px;
    }
}